@font-face {
  font-family: "Blair";
  src: local("Blair"), url("./media/Blaimim.ttf") format("truetype");
}
@import url("https://fonts.googleapis.com/css2?family=Playfair+Display:ital,wght@0,400;0,500;0,600;1,400&display=swap");

$mainRed: #a21a1e;

.particle-container {
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: 0;

  display: flex;
  align-items: center;
  justify-content: center;
}

body {
  background: linear-gradient(white, #d4d4d4);
}
.full-body {
  overflow-x: hidden !important;
}

.tbird-font {
  font-family: "Blair";
}

.playfair {
  font-family: "Playfair Display", serif;
}

.weight-400 {
  font-weight: 400;
}

.weight-500 {
  font-weight: 500;
}

.weight-600 {
  font-weight: 600;
}

.red {
  color: $mainRed;
}

section {
  width: 100vw;
  min-height: 100vh;
}
.home {
  align-items: center;
  justify-content: center;
  position: relative;
  .head-text {
    h1 {
      font-size: 3.5rem;
    }
    h2 {
      font-size: 2.75rem;
    }

    .playfair {
      font-size: 2.25rem;
    }
  }
}
